@font-face {
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-family: Avenir;
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}

@font-face {
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
  font-family: Avenir;
  font-style: normal;
  font-weight: 900;
  font-display: swap;
}

html {
  --form-text-color: #3d4e61;
}

.page__form {
  margin-top: 20px;
}

.form__label {
  display: block;
  position: relative;
}

.form__label:last-child {
  margin-top: 20px;
}

.form__label:after {
  content: "";
  background: url("Search.ccd3c07b.svg") 0 0 / cover no-repeat;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.form__label--search-icon--big:after {
  width: 19px;
  height: 19px;
  left: 26px;
}

.form__label--search-icon--small:after {
  width: 11px;
  height: 11px;
  left: 13px;
}

.form__search-bar {
  color: var(--form-text-color);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: Avenir, sans-serif;
  font-weight: 900;
}

.form__search-bar::placeholder {
  color: var(--form-text-color);
  font-weight: 300;
}

.form__search-bar--big {
  height: 70px;
  padding: 26px 26px 26px 62px;
  font-size: 16px;
  line-height: 22px;
}

.form__search-bar--big:hover, .form__search-bar--big:focus {
  box-shadow: 0 4px 4px #00000040;
}

.form__search-bar--small {
  height: 42px;
  padding: 12px 12px 12px 33px;
  font-size: 14px;
  line-height: 19px;
}

.form__search-bar--small:hover, .form__search-bar--small:focus {
  box-shadow: 0 3px 8px #3d4e6133;
}

.form__search-bar--big:focus, .form__search-bar--small:focus {
  background-image: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
}

